  }
  .light body::after {
    content: "";
    position: fixed;
    inset: -20%;
    pointer-events: none;
    z-index: -1;
    background-image: radial-gradient(48% 40% at 8% 8%, hsl(var(--primary) / 0.25), transparent 65%),
      radial-gradient(45% 45% at 92% 12%, hsl(var(--accent) / 0.2), transparent 64%);
    filter: blur(18px) saturate(1.05);
  }
  .bg-background,
  .bg-card,
  .bg-popover,
  .bg-sidebar {
    background-color: hsl(var(--background) / 0.34);
    background-image: linear-gradient(var(--app-surface-tint), var(--app-surface-tint));
    backdrop-filter: blur(16px);
  }
